import React,{useState} from "react";

function CreateArea(props) {
  //Creating a const object to hold the information and an object setNote to set the information to note
  const [note,setNote]=useState({
    title:"",
    content:""
  })
  //This will store the text written in their respective input area and recognized by name and value which is unique
  function handleChange(event){
    const {name,value}=event.target;
    setNote(prevNote=>{
      return {
        ...prevNote,
        [name]:value
      };
    })
  }
  function submitNote(event){
    props.onAdd(note);
    setNote({
      title:"",
      content:""
    });
    event.preventDefault();//This prevent the form from automating reloading

  }
  return (
    <div>
      <form>
        <input name="title" onChange={handleChange} value={note.title} placeholder="Title" spellcheck="false" />
        <textarea name="content" onChange={handleChange} value={note.content} placeholder="Take a note..." rows="3" spellcheck="false" />
        <button onClick={submitNote}>+</button>
      </form>
    </div>
  );
}

export default CreateArea;
