import React from "react";
import {Helmet} from "react-helmet";
function Note(props){
  function handleClick(){
    props.onDelete(props.id);
  }
  return (
    <div className="note">
    <h1>{props.title}</h1>
    <p>{props.content}</p>
    <button onClick={handleClick}><i className="fa-solid fa-trash"></i></button>
    <Helmet>
    <script src="https://kit.fontawesome.com/f67afcb444.js" crossorigin="anonymous"></script>
    </Helmet>
    </div>

  );
}
export default Note;
